import { BrowserRouter,Route } from 'react-router-dom'
import Home from './routes/Home'
import About from './routes/About';
import './App.css';


function App() {
  return (
    <div className="App">
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route exact path="/about">
          <About/>
        </Route>
      </BrowserRouter>
    </div>
  );
}

export default App;
